<template>
	<view class="navbar">
		<view class="navbar-fixed">
			<view :style="{height: statusBarHeight+'px'}"></view>
			<view class="navbar-content" :style="{height: navBarHeight+'px',width:navBarWidth+'px'}">
				<view class="navbar-serach">
					<view class="navbar-icon">
						<uni-icons type="search" color="#999" size="16"></uni-icons>
					</view>
					<input class="navbar-input" focus placeholder="搜索" />
				</view>
			</view>
		</view>
		<view :style="{height: statusBarHeight + navBarHeight + 'px'}"></view>
	</view>
</template>

<script>
	export default {
		name: "navbar",
		data() {
			return {
				statusBarHeight: 20,
				navBarHeight: 45,
				navBarWidth: 375
			};
		},
		created() {
			//获取设备状态信息
			const info = uni.getSystemInfoSync()
			this.statusBarHeight = info.statusBarHeight
			
			// #ifndef H5 || APP || MP-ALIPAY
			// 获取胶囊信息
			const menuInfo = uni.getMenuButtonBoundingClientRect()

			this.navBarHeight = (menuInfo.bottom - info.statusBarHeight) + (menuInfo.top - info.statusBarHeight)

			this.navBarWidth = menuInfo.left
			//  #endif


		}
	}
</script>

<style lang="scss">
	.navbar {
		.navbar-fixed {
			position: fixed;
			top: 0;
			left: 0;
			z-index: 99;
			width: 100%;
			background-color: $mk-base-color;

			.navbar-content {
				display: flex;
				align-items: center;
				justify-content: center;
				padding: 0 15px;
				box-sizing: border-box;

				.navbar-serach {
					width: 100%;
					height: 30px;
					background-color: #fff;
					border-radius: 30px;
					display: flex;
					align-items: center;
					padding: 0 10px;

					.navbar-icon {
						margin-right: 10px;
					}

					.navbar-input {
						color: #999;
						font-size: 12px;
					}
				}
			}

		}
	}
</style>
